.footer-mobile {
  margin-top: 40px;
  text-align: center;
  &__container {
    max-width: 768px;
    margin: 0 auto;
    background-color: #fff;
    box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
    padding: 120px 0;
  }
  &__logo {
    margin-bottom: 18px;
  }
  &__text {
    color: $dark75;
    margin-bottom: 26px;
  }
}
.mobile-socials {
  display: flex;
  justify-content: center;
  align-items: center;
  &__item {
    & + & {
      margin-left: 22px;
    }
  }
  &__link {
    &:hover {
      path {
        fill: rgb(132, 0, 255);
      }
    }
  }
}
